/* harmony default export */ var home = ({
    data: function data() {
        return {
            time: new Date(), //  时间
            date: new Date(), // 日期
            num: null, //  选择星期数组的序号
            weekDay: ["星期天", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
            scroll: null,
            name: '', //   当前在线人员
            Steps: 0 //计步
        };
    },

    methods: {
        onScroll: function onScroll(pos) {
            this.scroll = pos.left;
        },

        // 进入运动详情
        exercise: function exercise() {
            console.log("进入运动界面");
            this.$router.push('/exercise');
        }
    },
    watch: {
        scroll: function scroll() {
            if (this.scroll > 50) {
                this.$router.push("/operation");
            }
        }
    },
    // 时间过滤器
    filters: {
        // 获取当前时间函数
        timeFormate: function timeFormate(timeStamp) {
            var hh = new Date(timeStamp).getHours() < 10 ? "0" + new Date(timeStamp).getHours() : new Date(timeStamp).getHours();
            return hh;
        },

        // 获取当前时间函数
        timeFormateMin: function timeFormateMin(timeStamp) {
            var mm = new Date(timeStamp).getMinutes() < 10 ? "0" + new Date(timeStamp).getMinutes() : new Date(timeStamp).getMinutes();
            return mm;
        },

        // 获取当前日期函数
        dateFormate: function dateFormate() {
            var date = new Date();
            var strDate = date.getDate();
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            var currentdate = strDate;
            return currentdate;
        }
    },
    created: function created() {
        var _this2 = this;

        this.common.commonFun.ajax({
            dom: this,
            url: this.common.apiUrl.getUserInfo,
            type: "post",
            data: {},
            success: function success(response, allResponse) {
                _this2.name = response.username;
            }
        });
    },

    // 页面初始化
    mounted: function mounted() {
        this.num = this.date.getDay();
        var _this = this;
        this.timer = setInterval(function () {
            _this.time = new Date();
        }, 1000);
        apiready = function apiready() {
            var pedometer = api.require('pedometer');
            pedometer.getStepCount({
                count: 0,
                startTime: window.localStorage.getItem("login_time"),
                endTime: new Date()
            }, function (ret) {
                alert(ret.total);
                this.Steps = ret.total;
            });
        };
    },

    // 离开该页面的时候停止时间刷新
    beforeDestroy: function beforeDestroy() {
        if (this.timer) {
            clearInterval(this.timer);
        }
    }
});
